let WebIM = require("../../utils/WebIM")["default"];

export default {
  data() {
    return {
      username: "",
      password: "",
      psdFocus: "",
      nameFocus: ""
    };
  },

  components: {},
  props: {},
  onLoad: function () {
    let app = getApp().globalData;
    new (getApp().globalData.ToastPannel.ToastPannel)();
  },
  methods: {
    bindUsername: function (e) {
      this.setData({
        username: e.detail.value
      });
    },
    bindPassword: function (e) {
      this.setData({
        password: e.detail.value
      });
    },
    onFocusPsd: function () {
      this.setData({
        psdFocus: 'psdFocus'
      });
    },
    onBlurPsd: function () {
      this.setData({
        psdFocus: ''
      });
    },
    onFocusName: function () {
      this.setData({
        nameFocus: 'nameFocus'
      });
    },
    onBlurName: function () {
      this.setData({
        nameFocus: ''
      });
    },
    register: function () {
      const that = this;

      if (that.username == "") {
        return this.toastFilled('请输入用户名！');
      } else if (that.password == "") {
        return this.toastFilled('请输入密码！');
      } else {
        var options = {
          apiUrl: WebIM.config.apiURL,
          username: that.username.toLowerCase(),
          password: that.password,
          nickname: "",
          appKey: WebIM.config.appkey,
          success: function (res) {
            console.log('res', res);

            if (res.statusCode == "200") {
              that.toastSuccess('注册成功');
              var data = {
                apiUrl: WebIM.config.apiURL,
                user: that.username.toLowerCase(),
                pwd: that.password,
                grant_type: "password",
                appKey: WebIM.config.appkey
              };
              wx.setStorage({
                key: "myUsername",
                data: that.username
              });
            }
          },
          error: function (res) {
            console.log('res', res);

            if (res.statusCode !== "200") {
              if (res.statusCode == '400' && res.data.error == 'illegal_argument') {
                return that.toastFilled('用户名非法!');
              }

              that.toastFilled('用户名已被占用!');
            }
          }
        };
        WebIM.utils.registerUser(options);
      }
    },
    setData: function (obj, callback) {
      let that = this;
      let keys = [];
      let val, data;
      Object.keys(obj).forEach(function (key) {
        keys = key.split('.');
        val = obj[key];
        data = that.$data;
        keys.forEach(function (key2, index) {
          if (index + 1 == keys.length) {
            that.$set(data, key2, val);
          } else {
            if (!data[key2]) {
              that.$set(data, key2, {});
            }
          }

          data = data[key2];
        });
      });
      callback && callback();
    }
  }
};
